<template>
  <flexBox>
    <div class="title-1 col">
      
    </div>
    <titleSub class="col">
      
    </titleSub>
    <text-sub class="col">
      https://github.com/Slothingg/Xgun-components/blob/main/uni_components/media.vue
    </text-sub>
    <titleSub class="col">
      
    </titleSub>
    <text-sub class="col">
      uniapprpx,uniapp
    </text-sub>
    <titleSub class="col">
      
    </titleSub>
    <codemirror :value="code" :options="cmOptions"> </codemirror>
    <phone>
      <media class="media" :item="item" :theme="theme" :backgroundUrl="backgroundUrl">
      </media>
    </phone>
    <div class="title-1 col">
      Api
    </div>
    <apiTable :data="apis"></apiTable>
    <div class="title-1 col">
      Events
    </div>
    <eventTable :data="events"></eventTable>
    <div class="title-1 col">
      Slot
    </div>
    <slotTable :data="slots"></slotTable>
  </flexBox>
</template>

<script>
  import 'codemirror/mode/vue/vue.js'
  export default {
    data() {
      return {
        item: {
          avatar: '',
          time: '2020-12-12',
          username: 'balabala',
          already_follow: false,
          content: `abcdeasdfg<img src="../../static/emoji.gif"><img src="../../static/emoji.gif"><img src="../../static/emoji.gif"><img src="../../static/emoji.gif">asdasdasdasd`,
          already_zan: false, //
          num_zan: 0,
          already_chat: false,
          num_chat: 0,
          already_share: false,
          num_share: 0,
          comment: {
            username: 'abc',
            content: 'asddasdasasdasdasdsdasdasa'
          }
        },
        theme: '#2d8cf0',
        backgroundUrl: 'url("../static/diwen.jpeg")',
        cmOptions: {
          mode: "text/javascript",
          theme: "base16-dark",
        },
        apis: [{
            porps: 'item',
            instruction: '',
            type: 'Object',
            defalut: '{}'
          },
          {
            porps: 'order_tools',
            instruction: '',
            type: 'Boolean',
            defalut: 'false'
          },
          {
            porps: 'theme',
            instruction: '',
            type: 'String',
            defalut: ''
          }
        ],
        events: [{
            events: 'toUser',
            instruction: '',
            ret: 'item'
          },
          {
            events: 'zan,cancel_zan',
            instruction: '',
            ret: 'item'
          },
          {
            events: 'follow',
            instruction: '',
            ret: 'item'
          },
          {
            events: 'share',
            instruction: '',
            ret: 'item'
          }
        ],
        slots: [{
            name: 'media',
            instruction: '',
          },
          {
            name: 'tool',
            instruction: 'order_tools:true',
          }
        ],
        code: `
        <media :item="item" :theme="theme" :backgroundUrl="backgroundUrl"></media>

        backgroundUrl:'url("../static/diwen.jpeg")'
        theme:'#2d8cf0',//
        order_tools:false,//
        item: {
          avatar: '',
          time: '2020-12-12',
          username: 'balabala',
          already_follow: false,
          content: 'abcdefg',
          already_zan: false,
          num_zan: 0,
          already_chat: false,
          num_chat: 0,
          already_share: false,
          num_share: 0,
          comment: { //
            username: '',
            content: ''
          }
        }`
      }
    },
    components: {}
  }
</script>

<style scoped="scoped">
  .col {
    margin-top: 10px;
  }

  .row {
    display: flex;
    flex-direction: row;

  }


  @media screen and (max-width: 750px) {
    .row {
      flex-direction: column;
    }

    .xg-media-box .xg-media-head {
      height: 120px !important;
    }
  }
</style>



